<template>
  <div class="test-pages pa-3">
    <v-layout row>
      <v-flex sm8>
        <!-- Not Completed -->
        <div class="avenir-14-reg color-red">Status : Not Completed</div>
        <div class="avenir-14-reg">List Items</div>
        <div class="avenir-12-reg" v-for="(data, index) in items" :key="index">
          <span>- {{ data.name }}</span>
          <button
            class="mt-4 btn-hapus"
            @click="deleteItems(data.id)"
          > Hapus
          </button> <br />
          <!-- <span>Items: {{ data.items }}</span> -->
        </div>
        <div clas></div>

      <!-- Completed -->
      <div class="avenir-14-reg pt-7 color-green">Status : Completed</div>
      <div class="avenir-14-reg">List Items</div>

      </v-flex>
      <v-flex sm4>
        <div>
          <v-card-text class="avenir-14-reg mb-1 color-text-3">Nama Items</v-card-text>
          <v-text-field
            v-model="name"
            outlined
            background-color="white"
            placeholder="Masukkan Nama Items"
            dense
            fixed
            type="text"
            hide-details
          ></v-text-field>
        </div>
        <v-btn
          depressed
          color="primary"
          block
          height="48px"
          class="mt-4 btn-radius"
          :disabled="name === ''"
          @click="addItems()"
        > Tambah
        </v-btn>
      </v-flex>
    </v-layout>

  </div>
</template>
<script>
export default {
  data: function () {
    return {
      name: '',
      isLoaded: false
    }
  },
  computed: {
    items () {
      return this.$store.getters['checklist/state'].items
    }
  },
  methods: {
    async getAllItems () {
      // this.$loading('start')
      try {
        await this.$store.dispatch('checklist/getChecklist', {})
      } catch (err) {
        alert('Gagal Memproses')
        throw new Error(err)
      } finally {
        this.isLoaded = true
        // this.$loading('finish')
      }
    },
    async addItems () {
      // this.$loading('start')
      try {
        const payload = {
          name: this.name
        }

        const res = await this.$store.dispatch('checklist/saveChecklist', payload)

        if (res.message === 'Proses save berhasil' || res.statusCode === 2000) {
          alert('Berhasil tambah data')
          this.getAllItems()
        }
      } catch (err) {
        alert('Gagal Memproses')
        throw new Error(err)
      } finally {
        // this.$loading('finish')
      }
    },
    async deleteItems (id) {
      // this.$loading('start')
      try {
        const res = await this.$store.dispatch('checklist/deleteChecklist', id)

        if (res.message === 'Proses delete berhasil' || res.statusCode === 2300) {
          alert('Data berhasil di Hapus')
          this.getAllItems()
        }
      } catch (err) {
        alert('Gagal Memproses')
        throw new Error(err)
      } finally {
        // this.$loading('finish')
      }
    }
  },
  created () {
    // this.saveData()
    this.getAllItems()
  }
}
</script>
<style lang="sass">
.test-pages
  *
    margin: 0
    padding: 0

  .wrapper-login
    border: 1px solid #d3d3d3
    width: 50%
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)
    border-radius: 8px

  .btn-radius
    border-radius: 4px !important

  .action-register
    .color-blue
      color: #1976D2

  .btn-hapus
    margin-left: 8px
    padding: 6px
    background-color: salmon
    border: none
    border-radius: 4px
    color: white

</style>
